import React, { useState } from 'react';
import './css/Habilidades.css';

function ArsenalHabilidades() {
  const [activeIndex, setActiveIndex] = useState(null);

  const habilidades = [
    {
      nombre: 'Visin Estratgica',
      icono: '',
      descripcion: 'Diseo e implementacin de estrategias para impulsar el crecimiento empresarial.',
      ejemplos: [
        { texto: 'Evaluacin de Procesos', enlace: '#evaluacion-procesos' },
        { texto: 'Anlisis FODA', enlace: '#analisis-foda' },
        { texto: 'Transformacin Digital', enlace: '#transformacion-digital' },
      ],
    },
    {
      nombre: 'Liderazgo de Equipos',
      icono: '',
      descripcion: 'Gestin y desarrollo de equipos multidisciplinarios enfocados en alcanzar objetivos concretos.',
      ejemplos: [
        { texto: 'Metodologas giles', enlace: '#metodologias-agiles' },
        { texto: 'Capacitacin Personalizada', enlace: '#capacitacion-personalizada' },
        { texto: 'Gestin Remota', enlace: '#gestion-remota' },
      ],
    },
    {
      nombre: 'Integracin Tecnolgica',
      icono: '',
      descripcion: 'Automatizacin y digitalizacin con BuildShip, Render y AWS para optimizar flujos de trabajo.',
      ejemplos: [
        { texto: 'Automatizacin con AWS', enlace: '#automatizacion-aws' },
        { texto: 'Despliegue en Render', enlace: '#despliegue-render' },
        { texto: 'Integracin Continua', enlace: '#integracion-continua' },
      ],
    },
    {
      nombre: 'Soluciones AI Avanzadas',
      icono: '',
      descripcion: 'Implementacin de inteligencia artificial con OpenAI, TensorFlow, Ollama, y chatbots personalizados.',
      ejemplos: [
        { texto: 'Asistentes Virtuales', enlace: '#asistentes-virtuales' },
        { texto: 'Automatizacin AI', enlace: '#automatizacion-ai' },
        { texto: 'Chatbots', enlace: '#chatbots' },
      ],
    },
    {
      nombre: 'Anlisis de Datos',
      icono: '',
      descripcion: 'Extraccin, limpieza y anlisis con JavaScript, Python, SQL, BI y Tableau para decisiones basadas en datos.',
      ejemplos: [
        { texto: 'Anlisis Predictivo', enlace: '#analisis-predictivo' },
        { texto: 'Visualizacin de Datos', enlace: '#visualizacion-datos' },
        { texto: 'Dashboards BI', enlace: '#dashboards-bi' },
      ],
    },
    {
      nombre: 'Logstica y Operaciones',
      icono: '',
      descripcion: 'Optimizacin de operaciones y procesos para una mejora continua y sostenible.',
      ejemplos: [
        { texto: 'Optimizacin de Suministro', enlace: '#optimizacion-suministro' },
        { texto: 'Mejora en Almacenes', enlace: '#mejora-almacenes' },
        { texto: 'Gestin de Inventarios', enlace: '#gestion-inventarios' },
      ],
    },
    {
      nombre: 'E-Commerce & Marketplaces',
      icono: '',
      descripcion: 'Experiencia en digitalizacin, integracin y gestin en plataformas como Mercado Libre, Amazon y Shopify.',
      ejemplos: [
        { texto: 'SEO para Amazon', enlace: '#seo-amazon' },
        { texto: 'Integracin en Shopify', enlace: '#integracion-shopify' },
        { texto: 'SEO en Mercado Libre', enlace: '#seo-mercado-libre' },
      ],
    },
  ];

  const handleCardClick = (index) => {
    setActiveIndex(index === activeIndex ? null : index);
  };
  return (
    <section id="habilidades" className="arsenal-habilidades">
      <h2 className="titulo-seccion">Arsenal de Habilidades</h2>
      <p className="introduccion shinyh">
        Estas son algunas herramientas que utilizo para transformar y potenciar negocios. Cada una es clave en mi navaja suiza digital, diseada para enfrentar desafos y ofrecer soluciones innovadoras.
      </p>
      <div className="habilidades-grid">
        {habilidades.map((habilidad, index) => (
          <div
            key={index}
            className={`habilidad-card ${activeIndex === index ? 'active' : ''}`}
            onClick={() => handleCardClick(index)}
          >
            <div className="habilidad-header">
              <div className="habilidad-icono">{habilidad.icono}</div>
              <div className="habilidad-nombre">{habilidad.nombre}</div>
            </div>
            <div className="habilidad-descripcion">
              {habilidad.descripcion}
            </div>
            <ul className={`habilidad-ejemplos ${activeIndex === index ? 'active' : ''}`}>
              {habilidad.ejemplos.map((ejemplo, ejemploIndex) => (
                <li key={ejemploIndex}>
                  <a href={ejemplo.enlace} target="_blank" rel="noopener noreferrer">{ejemplo.texto}</a>
                </li>
              ))}
            </ul>
          </div>
        ))}
      </div>
    </section>
  );
}

export default ArsenalHabilidades;
