import React, { useState, useEffect } from 'react';
import './css/SobreMi.css';
import personal_story from './assets/files/childhood.png';
import professional from './assets/files/ecommerce_marketing.png';
import tech_vision from './assets/files/technologic_vision.png';
import { Link } from 'react-router-dom';
import { useSwipeable } from 'react-swipeable';
import { useSpring, animated } from '@react-spring/web';

function SobreMi() {
  const [currentSection, setCurrentSection] = useState(0);

  const sections = [
    {
      title: 'Historia Personal',
      image: personal_story,
      alt: 'Historia Personal',
      subtitle: 'Desde que tengo memoria, siempre he sentido una gran curiosidad por cmo funcionan las cosas.',
      content: (
        <>
          <p>
            Esa curiosidad me llev a desarmar juguetes, estudiar ingeniera, y finalmente, transformar empresas a travs de la tecnologa.
          </p>
          <p>
            Soy Elier Loya, un Ingeniero Industrial y de Sistemas con ms de 10 aos de experiencia en la direccin y optimizacin de operaciones. Mi pasin por la tecnologa y la innovacin me ha llevado a especializarme en transformacin digital y gestin logstica.
          </p>
          <p>
            A lo largo de mi carrera, he tenido el privilegio de liderar transformaciones clave en empresas como GoFarma, Farmalisto y PepsiCo, enfocndome en la digitalizacin, automatizacin y escalabilidad de procesos complejos.
          </p>
        </>
      ),
    },
    {
      title: 'Experiencia Destacada',
      image: professional,
      alt: 'Experiencia Destacada',
      subtitle: 'Mis logros reflejan mi compromiso con la excelencia operativa y mi pasin por transformar empresas.',
      content: (
        <>
          <h3>GoFarma</h3>
          <p>
            Como co-fundador y Director de Operaciones, dise e implement un modelo operativo innovador que logr una precisin de inventario del 99.8% y tiempos de entrega altamente competitivos.
          </p>
          <h3>Farmalisto</h3>
          <p>
            En mi rol como Director de Ventas en Marketplaces, lider la expansin de la presencia en lnea de la compaa, logrando un incremento del 144% en ventas y una reduccin significativa en reclamos y cancelaciones.
          </p>
        </>
      ),
    },
    {
      title: 'Visin Tecnolgica',
      image: tech_vision,
      alt: 'Visin Tecnolgica',
      subtitle: 'Creo en el poder de la tecnologa para transformar negocios y construir un futuro ms eficiente.',
      content: (
        <>
          <p>
            Hace algunos aos, tuve un momento de epifana mientras implementaba un sistema de automatizacin en una cadena logstica. Me di cuenta de que no solo estaba optimizando procesos, estaba cambiando la vida de las personas que trabajaban all, hacindoles la vida ms fcil y sus trabajos ms satisfactorios.
          </p>
          <p>
            Creo firmemente en el poder transformador de la tecnologa en los negocios. Mi enfoque se centra en aprovechar soluciones innovadoras como la IA, la automatizacin y el anlisis de datos para impulsar la eficiencia operativa y el crecimiento empresarial.
          </p>
          <p>
            Si compartes mi visin de un futuro ms eficiente y tecnolgicamente avanzado, me encantara que trabajramos juntos. Vamos a transformar tu negocio en una historia de xito.
          </p>
        </>
      ),
    },
  ];

  const handleSwipe = (eventData) => {
    if (eventData && eventData.dir) {
      const { dir } = eventData;
      if (dir === 'Left') {
        setCurrentSection((prevSection) => (prevSection + 1) % sections.length);
      } else if (dir === 'Right') {
        setCurrentSection((prevSection) => (prevSection - 1 + sections.length) % sections.length);
      }
    } else {
      console.warn('Swipe eventData is missing or does not have the dir property');
    }
  };

  const [props, api] = useSpring(() => ({
    opacity: 0,
  }));

  useEffect(() => {
    api.start({
      opacity: 1,
      reset: true,
      from: { opacity: 0 },
    });
  }, [currentSection, api]);

  const swipeHandlers = useSwipeable({
    onSwiped: handleSwipe,
    preventDefaultTouchmoveEvent: true,
    trackMouse: true,
  });

  return (
    <section id="sobre-mi" className="sobre-mi" {...swipeHandlers}>
      <div className="contenido-sobre-mi">
        <h2>Sobre M</h2>
        <div className="tabs">
          {sections.map((section, index) => (
            <button
              key={index}
              className={`tab-button ${currentSection === index ? 'active' : ''}`}
              onClick={() => setCurrentSection(index)}
            >
              {section.title}
            </button>
          ))}
        </div>
        <animated.div style={props} className="section-content">
          <div className="sub-title-container shinyy">{sections[currentSection].subtitle}</div>
          <div className="image-container">
            <img src={sections[currentSection].image} alt={sections[currentSection].alt} />
          </div>
          <div className="text-content">
            {sections[currentSection].content}
          </div>
        </animated.div>
        <div className="cta-seccion-sobre-mi">
          <Link to="/servicios" className="cta-button-2">Mis Servicios</Link>
          <Link to="/contacto" className="cta-button-2">Contctame</Link>
        </div>
        <div className="horizontal-scroll-indicator">
          {sections.map((_, index) => (
            <div
              key={index}
              className={`indicator ${currentSection === index ? 'active-indicator' : ''}`}
            />
          ))}
        </div>
      </div>
    </section>
  );
}

export default SobreMi;
