import React from 'react';
import './css/Servicios.css'; // Asegrate de que este archivo CSS est en la misma carpeta
import { Link } from 'react-router-dom';

function Servicios() {
  const servicios = [
    {
      nombre: "Consultora en Transformacin Digital",
      descripcion: "Asesoramiento estratgico para la implementacin de tecnologas digitales que optimicen procesos y mejoren la eficiencia operativa.",
      beneficios: [
        "Identificacin de oportunidades de digitalizacin",
        "Desarrollo de estrategias de transformacin a medida",
        "Implementacin y seguimiento de proyectos digitales"
      ]
    },
    {
      nombre: "Optimizacin de Operaciones y Logstica",
      descripcion: "Mejora de procesos operativos y logsticos para aumentar la eficiencia y reducir costos.",
      beneficios: [
        "Anlisis y rediseo de procesos",
        "Implementacin de sistemas de gestin de inventario",
        "Optimizacin de rutas y tiempos de entrega"
      ]
    },
    {
      nombre: "Implementacin de Soluciones IA",
      descripcion: "Desarrollo e integracin de soluciones de Inteligencia Artificial para automatizar tareas y mejorar la toma de decisiones.",
      beneficios: [
        "Chatbots y asistentes virtuales personalizados",
        "Sistemas de prediccin y anlisis avanzado",
        "Automatizacin de procesos mediante IA"
      ]
    },
    {
      nombre: "Desarrollo de Aplicaciones Personalizadas",
      descripcion: "Creacin de aplicaciones a medida para satisfacer necesidades especficas de negocio.",
      beneficios: [
        "Soluciones adaptadas a requerimientos nicos",
        "Integracin con sistemas existentes",
        "Mejora de la eficiencia y productividad"
      ]
    },
    {
      nombre: "Estrategias de E-commerce y Marketplaces",
      descripcion: "Diseo e implementacin de estrategias para optimizar la presencia y ventas en plataformas de comercio electrnico.",
      beneficios: [
        "Optimizacin de listings y contenido",
        "Estrategias de pricing y promociones",
        "Gestin de reputacin y servicio al cliente"
      ]
    }
  ];

  return (
    <div className="servicios">
      <h1>Servicios</h1>
      {servicios.map((servicio, index) => (
        <div key={index} className={`servicio servicio-${index}`}>
          <h2>{servicio.nombre}</h2>
          <p>{servicio.descripcion}</p>
          <h3>Beneficios:</h3>
          <ul>
            {servicio.beneficios.map((beneficio, i) => (
              <li key={i}>{beneficio}</li>
            ))}
          </ul>
          <Link to="/contacto" className="cta-button">Contctame</Link>
        </div>
      ))}
    </div>
  );
}

export default Servicios;
